.line-through{text-decoration:line-through;user-select:none}.categories{text-align:center;margin:20px 0 25px 0}.categories h3{font-size:2.2em;font-family:Roboto Mono,monospace;margin-bottom:1.13636em;color:#6b6a6a;text-transform:capitalize}.categories section{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.categories section>div:nth-of-type(3){-webkit-box-ordinal-group:0;-ms-flex-order:-1;order:-1}.categories section img{-webkit-transition:all 700ms, -webkit-box-shadow 200ms;transition:all 700ms, -webkit-box-shadow 200ms;transition:all 700ms, box-shadow 200ms;transition:all 700ms, box-shadow 200ms, -webkit-box-shadow 200ms}.categories section img:hover{-webkit-filter:grayscale(0%) blur(0px);filter:grayscale(0%) blur(0px);-webkit-transform:scale(1.005);transform:scale(1.005);-webkit-box-shadow:5px 5px 0 #d3d3d3;box-shadow:5px 5px 0 #d3d3d3}@media screen and (min-width: 767px){.categories{text-align:initial;margin:10px 0 50px 0}.categories section{display:grid;grid-template-columns:repeat(auto-fill, minmax(25%, 1fr));grid-auto-rows:minmax(100px, auto);grid-gap:24px}.categories section>div:nth-of-type(3){-webkit-box-ordinal-group:initial;-ms-flex-order:initial;order:initial}}
